import './input.css'
import { decimal} from './money.js'
window.generateOrder = () => {
	let form = document.getElementById('form')
	let name = form.name.value,
		cny = form.cny.value,
		rate = form.rate.value
	if(!name){
		alert('请输入商品名')
		return 
	}
	if(!cny){
		alert('请输入人民币价格')
		return
	}
	if(!rate){
		alert('请输入港币兑换人民币汇率')
		return
	}
	if(cny <= 0){
		alert('请输入合法的人民币价格')
		return
	}
	if(rate <= 0){
		alert('请输入合法的港币兑换人民币汇率')
		return
	}
	let params = {
		name: name,
		cny: cny,
		rate: rate
	}
	location.href = './detail.html?edit=' + encodeURIComponent(JSON.stringify(params))
}

window.updateHKD = () => {
	let form = document.getElementById('form')
	let name = form.name.value,
		cny = form.cny.value,
		rate = form.rate.value
	let HKD_ele = document.getElementById('HKD')
		
	if(!cny || !rate || cny <= 0 || rate <= 0){
		HKD_ele.innerHTML = '0'
	}else{
		HKD_ele.innerHTML = decimal(cny / rate)
	}
}